@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes dashOffset {
  from {
    stroke-dashoffset: 24;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.concept-node {
  animation: fadeIn 0.3s ease-out;
  transition: all 0.2s ease-out;
}

.concept-node:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1);
}

.concept-edge {
  stroke-dasharray: 4;
  animation: dashOffset 1s linear infinite;
}

.concept-map-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-image: radial-gradient(circle at 1px 1px, #f0f0f0 1px, transparent 0);
  background-size: 40px 40px;
}

.concept-map-content {
  position: absolute;
  inset: 0;
  transition: transform 0.1s ease-out;
}

.zoom-controls {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  gap: 0.5rem;
  z-index: 10;
}

.zoom-button {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
}

.zoom-button:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.node-details-modal {
  animation: fadeIn 0.2s ease-out;
}
